#links p, #contact h1, #contact li {
    text-align: center;
    margin: 4px;
}

#links p a, #contact li a {
    text-decoration: none;
    color: #cdcdcd;
    font-size: 24px;
}

#links p a:hover, #contact li a:hover {
    transition: 0.5s;
    color: #ffffff;
}

#contact h1 {
    color: #e4e4e4;
}
